<!--
  @auth:xdw
  echarts类阶梯型柱图
-->
<template>
  <div :id="randomID" :style="{width : params.canvasStyle.width, height: params.canvasStyle.height}"></div>
</template>

<script>
  // 引入基本模板
  let echarts = require("echarts/lib/echarts");
  //引入图表
  require("echarts/lib/chart/bar");
  require("echarts/lib/chart/line");

  // 引入提示框和标题组件
  require("echarts/lib/component/tooltip");
  require("echarts/lib/component/title");
  require("echarts/lib/component/legend");
  require("echarts/lib/component/markLine");
  require("echarts/lib/component/dataZoom");

  // import {filterKeyword} from "@/utils/filter";

  export default {
    name: "barVertical",
    props: {
      animateFlag: Boolean,
      params: {
        type: Object,
        default() {
          return {
            canvasStyle: {
              width: "1000px",
              height: "700px"
            },

            options: {
              colors: [], // 柱图颜色
              barBorderRadius: 4,
              barWidth: "20%",
              isVertical: false, //设置柱图是横向还是纵向默认不传 是横向
              yAxisLabel: true, //是否显示yaxisLabel
              map: {
                //数据映射 必传
                g: "高频",
                d: "低频",
                a: "未涉案"
              },
              xName: "name" //必传
            }
          };
        }
      }
    },
    data() {
      return {
        dataArr: [],
        colorArr: ["rgb(92,215,255)", "rgb(95,152,254)", "rgb(115,98,255)"]
        
      };
    },
    mounted() {
      this.$nextTick(() => {
        this.initdata();
        this.init();
      });
    },

    methods: {
      filterdata(keynae, data) {
        let res = data.map((item, index) => {
          return item[keynae];
        });
        return res;
      },
      initdata() {
        let keyarr = Object.keys(this.params.options.map);
        this.dataArr = [];
        keyarr.forEach((item, index) => {
          let arrs = this.filterdata(item, this.params.data);
          this.dataArr.push(arrs);
        });
      },
      init() {
        if (this.myCharts) {
          this.myCharts.dispose();
        }

        // 解决乘法运算出现多位小数的问题
        function accMul(arg1, arg2) {
          var m = 0, s1 = arg1.toString(), s2 = arg2.toString();
          try {
            m += s1.split(".")[1].length
          }
          catch (e) {
          }
          try {
            m += s2.split(".")[1].length
          }
          catch (e) {
          }
          return Number(s1.replace(".", "")) * Number(s2.replace(".", "")) / Math.pow(10, m)
        }

        let options = this.params.options;
        let paramsData = this.params.data;
        let linecolors = new Array(paramsData.length);
        linecolors.fill(this.$STANDARD.splitLineColor);
        let categoryData = this.filterdata(options.xName, this.params.data);
        let mapVal = Object.values(this.params.options.map);
        let mapkey = Object.keys(this.params.options.map);
        let seriesArr = [];
        if (mapkey.length != 1) {
          this.dataArr.forEach((item, index) => {
            let data = [];
            if (index == 0) {
              data = new Array(paramsData.length);
              data.fill(0);
            } else if (index == 1) {
              data = this.dataArr[index - 1];
              //  console.log(data)
            } else {
              data = paramsData.map((obj, ind) => {
                let sum = 0;
                mapkey.forEach((key, jk) => {
                  if (jk < index) {
                    sum += obj[key];
                  }
                });
                return sum;
              });
            }

            let series = [];
            series = [
              {
                name: "辅助",
                type: "bar",
                stack: `${index}`,

                itemStyle: {
                  normal: {
                    barBorderColor: "rgba(0,0,0,0)",
                    color: "rgba(0,0,0,0)"
                  },
                  emphasis: {
                    barBorderColor: "rgba(0,0,0,0)",
                    color: "rgba(0,0,0,0)"
                  }
                },
                data: data
              },
              {
                name: `${mapVal[index]}`,
                type: "bar",
                stack: `${index}`,
                barWidth: options.barWidth ? options.barWidth : 33.3,
                barGap: 0,
                itemStyle: {
                  normal: {
                    barBorderRadius: options.barBorderRadius
                      ? options.barBorderRadius
                      : 4,
                    color:  options.colors
                        ? options.colors[index]
                        : this.colorArr[index],
                    borderColor: options.colors
                      ? options.colors[index]
                      : this.colorArr[index],
                    borderWidth: 3,
                    label: {
                      show: true,
                      position: 'inside',
                      color: options.labelColor ? options.labelColor :  options.colors
                        ? options.colors[index]
                        : this.colorArr[index],
                      fontFamily: "Benmo",
                      fontSize: 20,
                      formatter: (par) => {
                        let str = "";
                        str += par.seriesName + ':';
                        str += accMul(par.data, 100) + "%";
                        return str;
                      }
                    }
                  }
                },
                data: item
              }
            ];

            seriesArr = [...seriesArr, ...series];
          });
        } else {
          let series = [];
          let percentData = [],
            data = this.filterdata(mapkey[0], paramsData),
            percentHolderData = [0],
            fix = 0,
            count = 0,
            total = 0;
          total = data.reduce((a, b) => a + b);

          data.forEach((item, index) => {
            // index % 3 == 0;
            fix = item / total * 100;
            percentData.push(fix);
            count += fix;
            percentHolderData.push(count);
            // categoryData.push(1);
          });
          percentHolderData.pop();
          series = [
            {
              name: "辅助",
              type: "bar",
              stack: 1,

              itemStyle: {
                normal: {
                  barBorderColor: "rgba(0,0,0,0)",
                  color: "rgba(0,0,0,0)"
                },
                emphasis: {
                  barBorderColor: "rgba(0,0,0,0)",
                  color: "rgba(0,0,0,0)"
                }
              },
              data: percentHolderData
            },
            {
              name: `${mapVal[0]}`,
              type: "bar",
              stack: 1,
              barWidth: options.barWidth ? options.barWidth : 33.3,
              barGap: 0,
              itemStyle: {
                normal: {
                  barBorderRadius: options.barBorderRadius
                    ? options.barBorderRadius
                    : 4,
                  color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [
                    {offset: 0, color: "rgb(112,98,215)"},
                    {offset: 0.5, color: "rgb(112,163,255)"},
                    {offset: 1, color: "rgb(92,216,215)"}
                  ]),
                  label: {
                    show: true,
                    color: "#fff",
                    fontFamily: "Benmo",
                    fontSize: 16,
                    formatter: function (par) {
                      let str = "";
                      str += categoryData[par.dataIndex];
                      str += "\n" + data[par.dataIndex];
                      return str;
                    }
                  }
                }
              },
              data: percentData
            }
          ];
          seriesArr = [...series];
        }

        let yAxis = [
          {
            type: "category",
            splitLine: {
              show: true,
              lineStyle: {
                // linecolors
                color: '#274377' //网格线颜色（所有图表需要统一）
              }
            },

            axisTick: {
              show: false
              // length: 100
            },
            axisLabel: {
              show: options.yAxisLabel ? true : false,
              color: '#fff',
              // color: this.$STANDARD.axisColor,
              //轴与标签的距离
              margin: 15,
              fontSize: 17
            },
            axisLine: {
              show: true,
              lineStyle: {
                type: "solid",
                // color: this.$STANDARD.axisColor,
                color: '#274377',
                // width: "1.5"
              }
            },
            data: categoryData
          }
        ];
        let xAxis = [
          {
            type: "value",
            axisTick: {
              show: false,
              interval: 0
            },
            splitNumber: options.splitNumber ? options.splitNumber : 5,
            splitLine: {
              show: true,
              lineStyle: {
                // color: this.$STANDARD.splitLineColor
                color: '#274377'
              }
            },
            axisLabel: {
              // color: this.$STANDARD.axisColor,
              color: '#fff',
              //轴与标签的距离
              margin: 15,
              fontSize: this.$STANDARD.h5.fontSize,
              formatter(params) {
                return params * 100 + "%";
              }
            },
            axisLine: {
              show: true,
              lineStyle: {
                type: "solid",
                // color: this.$STANDARD.axisColor,
                color: "#fffefd",
                width: "1.5"
              }
            }
          }
        ];
        let option = {
          tooltip: {
            trigger: "axis",
            backgroundColor: this.$STANDARD.tooltipBackgroundColor,
            borderColor: this.$STANDARD.tooltipBorderColor,
            borderWidth: 1,
            padding: 12,
            axisPointer: {
              type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
            },
            formatter(params) {
              var result = params[0].name + "<br/>";
              params.forEach(item => {
                if (item.seriesName == "辅助") {
                  result += "";
                } else {
                  if (item.data % 1 != 0) {
                    result +=
                      item.seriesName +
                      ":" +
                      accMul(item.data, 100) +
                      "%" +
                      "<br/>";
                  } else {
                    result += item.seriesName + ":" + accMul(item.data, 100) + "%" + "<br/>";
                  }
                }
              });
              return result;
            }
          },
          legend: {
            show: false,
            data: mapVal,
            right: 30,
            selectedMode: false,
            textStyle: {
              color: "rgb(255,254,253)",
              fontSize: 15
            },
          },
          // grid: {
          //   right: "20%"
          // },
          yAxis: options.isVertical ? xAxis : yAxis,
          xAxis: options.isVertical ? yAxis : xAxis,
          series: seriesArr,
          animationDelay: function (idx) {
            // 越往后的数据延迟越大
            return idx * 100;
          }
        };
        let wraper = document.getElementById(`${this.randomID}`);
        this.myCharts = echarts.init(wraper);
        this.myCharts.setOption(option);
      }
    },

    computed: {
      randomID() {
        return Math.random()
          .toString(16)
          .slice(2, 10);
      }
    },
    watch: {
      animateFlag: {
        handler(newVal, oldVal) {
          this.initdata();
          this.init();
        },
        deep: true
      },
      params: {
        handler(newVal, oldVal) {
          this.initdata();
          this.init();
        },
        deep: true
      }
    }
  };
</script>

<style lang="stylus" scoped>
</style>
