<template>
  <div class="wrapper">
    <div class="content">
      <div class="w-top">
				<ctitle :title="'涉件关联政策分类分析'" class="w-l-title"></ctitle>
				<div class="echarts">
        	<BarLineTuitie :animateFlag='animateFlag' :params='chartParams'></BarLineTuitie>
				</div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
	import ctitle from "@/components/title/index";
	import BarLineTuitie from '@/components/bar/BarLineTuitie';
  export default {
    name: 'sjfx',
    components: {
			ctitle,
			BarLineTuitie
    },
    data() {
      return {
				animateFlag: true,
				chartParams: {
          data: [],
          canvasStyle: {
            width: '100%',
            height: '100%'
          },
          options: {
						barBorderRadius: 4,
						labelColor: '#fff',
            isVertical: false,
            yAxisLabel: true,
            barWidth: '33.3%',
            colors: ['#5cd7ff', '#5f98fe', '#6f62fd'],
            map: {
              gp: '高频',
              dp: '低频',
              wsa: '未涉案'
            },
            xName: 'name'
          }
        }
      }
		},
		mounted() {
			this.getAPI(this.year)
		},
		methods: {
			getAPI(year) {
				get_http("zcglfxSjfxZcflfx", {
					year: year
        })
				.then(res => {
					this.chartParams.data = res.data.data;
					this.chartParams.options.map = res.data.map
				})
				.catch(err => {
					console.log(err);
				})
			}
		},
		computed: {
      year() {
        return this.$store.state.year;
      }
    },
    watch: {
      year(val) {
        this.getAPI(val);
      }
    }
  }
</script>

<style lang="stylus" scoped>
.wrapper
	position relative
	top 0.45rem
	height calc(100% - 45px)
	.content
		width 100%
		height 100%
		display flex
		padding .2rem .3rem
		box-sizing border-box
		flex-direction column
		.w-top
			flex 1
			display flex
			flex-direction column
			.echarts
				flex 1
</style>